
body{
  padding: 0;
  margin: 0;
  color: #2c3e50;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #F6F6F6;
  font-size: 14px;
}
header {
  font-family: 'Cabin Sketch', 'Avenir', Helvetica, Arial, sans-serif;
  text-align: center;
  padding-top: 50px;
  padding-bottom: 15px;
  background: #41B883;
  color: #fff;
}
main {
  margin: 0 auto;
  padding: 15px;
  max-width: 800px;
  margin-bottom: 100px;
  background: #fff;
}
blockquote {
  margin: 0;
  padding: 0.05em 1em;
  color: #6a737d;
  border-left: 0.25em solid #dfe2e5;
  background: #f3f3f3;
}
a{
  color: #0366D6;
  text-decoration: none;
}
.hljs{
  background: #FAFAFA;
}
code {
  color: #ED4C6B;
  background-color: #F6F8FA;
  margin: 0 4px;
  display: inline-block;
  padding: 1px 5px;
  font-size: 12px;
  border-radius: 3px;
  line-height: 18px;
  font-family: "SFMono-Regular",Consolas,"Liberation Mono",Menlo,Courier,monospace;
}

.m-button {
  color: #fff;
}
h1 {
  font-weight: 200;
}

.layout-footer{
  height: 100px;
}
.copyright{
  margin: 0;
  padding: 0;
  font-size: 12px;
  list-style: none;
  color: #586069;
  font-weight: 100;
}
.copyright li {
  display: inline-block;
  margin-left: 10px;
}

.fork-img{
  position: absolute;
  top: 0;
  right: 0;
  border: 0;
}

.demo-block{
  border: 1px solid #ebebeb;
  border-radius: 3px;
  -webkit-transition: .2s;
  transition: .2s;
  padding: 0px;
  margin: 10px 0;
}
.demo-block .source{
  padding: 24px;
}
.demo-block .description {
  padding: 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 1px solid #ebebeb;
  border-radius: 3px;
  font-size: 14px;
  line-height: 22px;
  color: #666;
  word-break: break-word;
  margin: 10px;
  background-color: #fff;
}
.demo-block .description p{
  padding: 0;
  margin: 0;
}
.demo-block .meta {
  background-color: #fafafa;
  border-top: 1px solid #eaeefb;
  overflow: hidden;
}
.demo-block body,
.demo-block .meta .hljs{
  background: transparent;
}
